*{margin: 0; padding: 0;}

body {
    height: 1000px;
}

.boxes {
  height: 300px;
  width: 100vw;
  background: yellow;
  margin-top: 100px;
}

.hero {
  height: 700px;
  width: 100vw;
}
.heroImage {
  background-image: url('../src/Images/2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 50px;
  position: fixed;
  top: -50px;
  z-index: -100;
}

