a {
  text-decoration: none;
}
nav {
  box-sizing: border-box;
  /* background: #8a898900; */
  background-color: #442f2f4d;
  height: 100px;
  display: flex;
  justify-content: space-around;
  /* border-bottom: 0px solid #eee; */
  /* box-shadow: 1px 0.5px 10px 0.8px #1113; */
  /* box-shadow: 1px 0.5px 1px 0.8px #1113; */
}

.logo {
  display: flex;
  align-items: center;
}

h1 {
  color: rgb(220, 38, 38);
  /*color-> rgb(220, 38, 38) */
  /* text-shadow->-0.6px 8.5px 4px #ff4b07 */
  text-shadow: 1px 1px 1px rebeccapurple;
  margin: 0;
  padding: 0;
  font-size: 2em;
  font-family: Helvetica, Arial, sans-serif;
  cursor: grab;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  /* justify-content: space-around; */
}


li {
  margin: 0 30px;
  padding: 0 5px;
}

a {
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  /* text-shadow: 1px 20px 1px #333; */
  text-shadow: 1px 1px 1px #333;
}

a:hover {
  color: #000;
}











